h1 {
    font-size: 2rem;
}

/* Home Page */

.index .row {
    width: 90%;
}

/* Projects Page */

.projects .row {
    margin: 0 auto;
    width: 95%;
    text-align: center;
}

.projects .project-box {
    text-align: center;
    background-color: #202020a2;
    box-shadow: 5px 5px #8181814b;
    margin: 2rem 1rem;
    padding: 0 2rem 2rem 2rem;
}

.projects img { 
    width: 10rem;
    margin: 0.5rem 0.5rem;
    float: center;
}

.projects p {
    color: white;
    font-size: 1rem;
    float: left;
}

.projects h2 {
    color: white;
    font-size: 2rem;
    padding: 1.5rem 0.3rem;
    width: inherit;
    height: 3rem;
    margin: 0 0 3.5rem 0;
}


/* Contacts Page */


.contact .row {
    max-width: 90%;
    margin: 0 auto;
}

.contact .contact-item {
    margin: 1.5rem 1.5rem;
    padding: 2rem 2rem;
    box-shadow: 5px 5px #8181814b;
    max-width: 90%
}

.contact .box1 {
    background-color: #ff161663;
}
.contact .box2 {
    background-color: #64ffac78;
}
.contact .box3 {
    background-color: #7dc7f178;
}
.contact .box4 {
    background-color: #946dff9b;
}

.contact .box3 a, .contact .box4 a {
    color: #f6ec8cdc;
}

.contact .col {
    text-align: center;
}

.contact .col img {
    width: 5rem;
    margin: 1rem auto;
}

.contact .col h2 {
    color: white;
    font-size: 1.3rem;
}

.title {
    max-width: 30rem;
    background-color: #90818136;
    box-shadow: 5px 5px #7a7a7a4b;
    padding: 1rem 1rem;
    margin: 1rem auto 2rem auto;
    color: white;
    max-height: 5rem;
    text-align: center;
}